.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}



/* custom card CSS */
.outer {
  display: grid;
  place-items: center;
  background-color: rgb(242, 242, 251);
  width: 350px;
  height: 500px;
  padding: 10px;
  border: 1px solid rgb(223, 223, 223);
  border-radius: 5px;
}

.outer:hover {
  border: 1px solid rgb(0, 0, 0,0.3);
}

.outer .image {
  position: relative;
  height: 400;
  width: 320px;
  overflow: hidden;
  border-radius: 2px;

}

.outer .image img {
  object-fit: fill;
  height: 200px;
  width: 100%;
  transition: transform ease-in-out 0.2s;
  transform: scale(1.1);
  border-radius: 2px;


}

.outer .image img:hover {
  transform: scale(1);
}

.outer h4 {
  width: 100%;
  height: auto;
  font-size: 20px;
  word-wrap: break-word;
  padding: 2px;
  line-height: 25px;
  margin-top: 5px;
  word-spacing: 2px;
}

.outer p {
  width: 100%;
  word-break: keep-all;
  height: auto;
}

.outer .viewButton {
  border: 1px solid blue;
  background: transparent;
  width: 100%;
  border-radius: 5px;
  height: 35px;
  letter-spacing: 3px;
  font-weight: bold;
  font-size: 1rem;
  transition: background-color ease-in-out 0.2s;
}

.outer .viewButton a {
  text-decoration: none;
  color: blue;
  padding: 5px 33% 5px 33%;
}

.outer .viewButton a:hover {
  color: black;
}

.outer button:hover {
  background-color: rgb(184, 215, 255);
  border: 1px solid rgb(189, 189, 189);
}
